.reg-modal-backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;

  opacity: 0;
  overflow-y: scroll;
  transition: opacity 0.3s ease-out;
  background-color: rgba(46, 47, 66, 0.4);
  z-index: 990;
}

.reg-modal-backdrop.show {
  opacity: 1;
}

.reg-form-popup {
  position: absolute;
  float: left;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  box-sizing: border-box;

  background-color: var(--header-color);
  width: 350px;
  height: auto;

  padding-top: 30px;
  border: 2px solid black;
  border-radius: 18px;
  z-index: 9999;
}

.reg-form-register {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  gap: 8px;
  padding: 20px;
}

.close-btn {
  position: absolute;
  top: 24px;
  right: 24px;
  display: flex;
  width: 20px;
  height: 20px;

  border-radius: 50%;
  justify-content: center;
  align-items: center;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  background-color: transparent;
  border: none;
  cursor: pointer;
	transition: transform 100ms cubic-bezier(0.4, 0, 0.2, 1);
}
.close-btn:hover{
	  top: 22px;
  right: 22px;
	width: 23px;
  height: 23px;
}
.reg-input-forms {
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;

  letter-spacing: -0.01em;
  text-transform: uppercase;

  color: var(--text-color);
  display: flex;
  flex-direction: column;
  border: 2px solid;
  border-color: var(--text-color);
  justify-content: center;
  align-items: flex-start;
  padding: 16px 11px;
  gap: 10px;

  width: auto;
  height: 100%;

  background-color: var(--header-color);
  border-radius: 40px;
  margin-bottom: 15px;
transition: transform, 300ms cubic-bezier(0.4, 0, 0.2, 1);
}

.btn-queue-modal {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  padding: 16px 116px;
  gap: 10px;
box-shadow: 1px -100px 86px -37px rgba(113, 73, 211, 0.06);
  width: auto;
  height: 100%;

  background-color: var(--text-color);
  border: 2px solid;
  border-color: var(--text-color);
  border-radius: 40px;

  font-weight: 700;
  font-size: 14px;
  line-height: 18px;

  letter-spacing: -0.01em;
  text-transform: uppercase;

  color: var(--header-color);
  transition: background-color 300ms cubic-bezier(0.4, 0, 0.2, 1);
}

.btn-queue-modal:hover,
.btn-queue-modal:focus {
  background-color: var(--yellow-color);
  color: var(--text-color);
}

.clicked {
  transform: translateY(2px);
  transition: transform 0.2s ease-out;
}

.register-links {
  text-decoration: none;
  display: flex;
  justify-content: center;
  gap: 20px;
  padding-top: 10px;
  padding-bottom: 20px;
  color: var(--text-color);
}

.reg-signin-btn {
  font-weight: 700;
  line-height: 1.4;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: -0.01em;
  text-transform: uppercase;
  color: var(--sing-btn);
}

.reg-signin-btn:hover,
.reg-signin-btn:focus,
.reg-signin-btn:active {
  color: var(--blue-color);
  text-decoration-line: underline;
}

.reg-signup-btn {
  font-weight: 700;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: -0.01em;
  text-transform: uppercase;
  color: var(--sing-btn);
}

.reg-signup-btn:hover,
.reg-signup-btn:focus,
.reg-signu-btn:active {
  color: var(--blue-color);
  text-decoration-line: underline;
}

@media screen and (min-width: 768px) {
  .reg-form-popup {
    width: 500px;
    padding-top: 40px;
  }
}
